'use client';
import React from 'react'
import { useState, useEffect } from 'react';
import { PDFDocument } from 'pdf-lib'; // For parsing PDF in the browser

const UploadResume = () => {
  const [file, setFile] = useState<File | null>(null);

  //Actual Pdf Text to be parsed
  const [text, setText] = useState<string | null>(null);

  //If Pdf is slected or not
  const [status, setStatus] = useState<string | null>(null);

  //Whenever file is uploaded, we set the current file to file
  const handleFileChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    if (event.target.files && event.target.files[0]) {
      setFile(event.target.files[0]);
    }
  };

  //To Parse and Make Embedding of PDF
  const handleUpload = async () => {
    if (!file) {
      setStatus('No file selected.');
      return;
    }

    try {
      // Step 1: Convert the file to an ArrayBuffer
      const arrayBuffer = await file.arrayBuffer();

      // Step 2: Load the PDF document
      const pdfDoc = await PDFDocument.load(arrayBuffer);

      // Step 3: Extract text from all pages
      const textArray = await Promise.all(
        pdfDoc.getPages().map(async (page) => {
          const textContent = page.getTextContent();
          return textContent;
        })
      );

      // Step 4: Concatenate and log the text
      const extractedText = textArray
        .map((content) => content.items.map((item: any) => item.str).join(' '))
        .join('\n');
      console.log(extractedText);

      // Save the text to state for further use
      setText(extractedText);
      setStatus('Text extracted successfully.');
    } catch (error) {
      console.error('Error extracting text:', error);
      setStatus('Failed to extract text from the file.');
    }
  };

    

  return (
    <div>
      <input type="file" accept="application/pdf" onChange={handleFileChange} />
      {file && <p>Uploaded file: {file.name}</p>}
    </div>
  );
};

export default UploadResume;